import ScheduleItem from 'press-ui/press-schedule-list/press-schedule-item.vue';
import PressPopupCell from 'press-ui/press-popup-cell/press-popup-cell.vue';
import PressPicker from 'press-ui/press-picker/press-picker.vue';
import PressCell from 'press-ui/press-cell/press-cell.vue';

import { formatScheItemData } from 'press-ui/press-schedule-list/helper/sche-item';

import {
  FUNCTIONAL_ID_MAP,
} from 'src/packages/press-popup-cell/demo-helper/helper';

import { showCustomPopup, local } from 'src/packages/press-schedule-list/demo-helper/custom';
import {
  STATUS_SCHE_MAP,
} from 'src/packages/press-schedule-tree/demo-helper/helper';
import { genMockScheduleItemData } from 'src/packages/press-schedule-list/demo-helper/mock-data';


export default {
  i18n: {
    'zh-CN': {
      custom: '自定义',
    },
    'en-US': {
      custom: 'Custom',
    },
  },
  components: {
    ScheduleItem,
    PressPopupCell,
    PressPicker,
    PressCell,
  },
  data() {
    const scheLocal = {
      ...local,
      isPreview: STATUS_SCHE_MAP[local.status]?.isPreview || 0,
      isAdmin: false,

      myTeamId: 'TEAM_ID_A',
      canShowOrderButton: true,
      scheSubStatusMap: {},
    };

    return {
      sectionStyle: 'margin: 0;background: #f7f8fa;',
      headerStyle: 'background: #f7f8fa;',
      FUNCTIONAL_ID_MAP,

      scheLocal,
      scheInfo: {},
    };
  },
  mounted() {
    this.generateData();
  },
  methods: {
    onShowBasicPopupCell() {
      showCustomPopup({
        context: this,
        callback: {
          changeStatus: this.changeStatus,
          changeAdmin: this.changeAdmin,
        },
      });
    },
    changeAdmin(isAdmin) {
      this.scheLocal = {
        ...this.scheLocal,
        isAdmin,
      };
      this.generateData();
    },
    changeStatus(status) {
      this.scheLocal = {
        ...this.scheLocal,
        status,
        isPreview: STATUS_SCHE_MAP[status].isPreview || 0,
      };
      this.generateData();
    },
    generateData() {
      const {
        isPreview,
        isAdmin,
        status,

        myTeamId,
        canShowOrderButton,
        scheSubStatusMap,
      } = this.scheLocal;
      const mockData = genMockScheduleItemData({
        info: STATUS_SCHE_MAP[status],
      });

      this.scheInfo = formatScheItemData({
        scheInfo: mockData,
        isPreview,
        isAdmin,

        myTeamId,
        canShowOrderButton,
        scheSubStatusMap,
      });
    },
  },
};
